@use "../styles/utils/mixins" as *;
@use "../styles/fonts";

.contact {

  &__form {
    @include grid;
    padding-bottom: 120px;

    @include for-large {
      padding-bottom: 183px;
    }
  }

  &__input {
    height: 48px;
    border-radius: 40px;
    background: #f8f8fa;
    text-indent: 16px;
    color: $active-input;
    font-size: 14px;
    outline: none;

    @include for-input-and-textarea();

    @include for-medium {
      grid-column: 1 / 4;
    }

    @include for-large {
      grid-column: 1 / 6;
    }

    &:hover {
      border: 1px solid $input-color;
      transform: scale(1.1);
      transition: transform 0.3s ease;
    }

    &:active {
      border: 1px solid $active-input;
    }

    &::placeholder {
      color: #7e7e83;
      font-size: 14px;

      font-weight: 400;
      line-height: 20px;
      text-indent: 16px;
    }
  }

  &__textarea {
    display: flex;
    padding: 14px 16px;
    align-items: flex-start;
    gap: 10px;
    border-radius: 16px;
    border: 1px solid $active-input;
    background: #f8f8fa;
    color: $active-input;
    font-size: 14px;
    resize: none;
    outline: none;
    margin-bottom: 16px;

    @include for-input-and-textarea();

    @include for-medium {
      grid-column: 1 / 4;
    }

    @include for-large {
      grid-column: 1 / 6;
    }

    &:hover {
      border: 1px solid $input-color;
      transform: scale(1.1);
      transition: transform 0.3s ease;
    }

    &:active {
      border: 1px solid $active-input;
    }

    &::placeholder {
      color: #7e7e83;
      font-size: 14px;

      font-weight: 400;
      line-height: 20px;
      text-indent: 0;
    }
  }

  &__button {
    width: 278px;
    cursor: pointer;
    outline: none;

    @include for-medium {
      margin-bottom: 120px;
    }

    @include for-large {
      margin-bottom: 182px;
      grid-column: 1 / 6;
      width: 411px;
    }
  }

  &__box {
    &--1 {
      grid-column: 1 / 3;

      @include for-medium {
        grid-area: 1 / 4 / 2 / 7;
      }

      @include for-large {
        grid-area: 1 / 7 / 2 / 13;
      }
    }

    &--2 {
      grid-column: 1 / 3;

      @include for-medium {
        grid-area: 2 / 4 / 3 / 7;
      }

      @include for-large {
        grid-area: 2 / 7 / 3 / 13;
      }

    }

    &--3 {
      grid-column: 1 / 3;

      @include for-medium {
        grid-area: 3 / 4 / 4 / 7;
      }

      @include for-large {
        grid-area: 3 / 7 / 4 / 13;
      }
    }
  }

  &__header {
    color: $white;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    margin: 0;
  }

  &__paragraph {
    color: $white;
    text-decoration: none;
    font-size: 16px;
    font-weight: $border-color;
    line-height: 140%;
    margin: 0;
    margin-bottom: 24px;

    &:hover {
      text-decoration: underline;
    }
  }
}
